import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    step: 1
  },
  mutations: {
    UPGRADE (state, payload) {
      state.step = payload + 1
    },
    UPGRADE_1 (state) {
      state.step = 2
    },
  },
  actions: {
  },
  getters: {
  }
})
